import React, {Component} from "react";
import PropTypes from "prop-types";
import {Graphic} from "./Utils"

const ProductRow = (props) => {
    return (
        <div className={`l-row u-padding-l-xl u-padding-top-l-xl u-small-padding-top-l u-small-padding-l ${props.borderBottom && 'u-border-bottom'}`}>
            <div className="l-col l-col--auto">
                <div className="opl-checkout__icon__cell u-text-center">
                    <Graphic icon={props.product.thumbnailIcon}
                             imgUrl={props.product.imageUrl}/>
                </div>
            </div>
            <div className="l-col l-col-8">
                <span className="u-font-bold u-small-font-large opl-word-wrap"
                      dangerouslySetInnerHTML={{__html: props.product.name}}/>
                <p className="u-small-padding-top-s" dangerouslySetInnerHTML={{__html: props.product.description}}/>
                {props.product.additionalContent}
            </div>
        </div>
    );
}

ProductRow.propTypes = {
    product: PropTypes.shape({
        name: PropTypes.string,
        thumbnailIcon: PropTypes.string,
        imageUrl: PropTypes.string,
        description: PropTypes.string,
        additionalContent: PropTypes.object
    }),
    borderBottom: PropTypes.bool
}

ProductRow.defaultProps = {
    borderBottom: true
}

export default ProductRow;